//MEDIA: 767
@media screen and (max-width: 767px) {
	.promo {
		height: 100vh;
		background: #000;
	}
	.promo-link {
		display: none;
	}
	.logo {
		padding: 20px 0;
		width: 120px;
	}
	#scene {
		// height: 80vh;
		canvas {
			// transform:translateX(-50%) scale(0.8);
			// top:-10vh;
		}
	}
	.jelly-container, #dote {
		display: none;
	}
	* {
		cursor: initial;
	}
	.mtitle {
		font-size: 26px;
		bottom: inherit;
		top:50%;
		transform: $z;
	}
	.wrapp-circle {
		width: 100vw;
		height: 100vw;
	}

	.portfolio {
		&-item {
			width: 100%;
			padding: 0;
		}
	}
	.burger-nav {
		top: inherit;
		bottom: 25px;
		right: 20px;
		height: 20px;
	}
	.social {
		top: inherit;
		bottom: 20px;
		left: 20px;
	}
	.wrapp-circle-2 {
		width: 120vh;
		height: 120vh;
	}
	nav {
		ul {
			flex-direction: column;
			li {
				a {
					font-size: 70px;
					padding: 10px 0;
				}
			}
		}
	}
	.start {
		border:none;
		width:115px;
		padding: 0;
		border-radius:0;
		height: auto;
		font-size: 14px;
		transform:$x;
		animation:none;
		bottom: 28px;
		&:hover {
			color: #FFF;
			&:after {
				content: none;
			}
		}
	}
	.social {
		a {
			width: 26px;
		}
	}
}

