@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');

$y:translateY(-50%);
$x:translateX(-50%);
$z:translateX(-50%) translateY(-50%);
$ff:'Montserrat', sans-serif;

html {
	box-sizing: border-box;
	font-size: 16px;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	height: 100%;
	position: relative;
}

body {
	font-family: $ff;
	background: #fff;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 100px;
	text-transform: uppercase;
}
input, textarea{
	border:none;
	outline: none;
	background: transparent;
}
button:focus,
textarea:focus,
input:focus {
	outline: transparent;
}

button {
	@include no-btn;
}

a {
	text-decoration: none;
	color: inherit;
}

// header 
.info-line{
