			align-items: center;
			align-items: flex-start;

			.search-wrapper{
				position: relative;
				display: flex;
				align-items: flex-start;
				.search-icon{
					margin-left: 10px;
					margin-right: 20px;
				}
				.search-input{
						color: #000;
						&::placeholder{
							color: #848484
						}
				}
				&::after{
					position: absolute;
					bottom: -7px;
					left: 0;
					content: '';
					width: 100%;
					height: 2px;
					background: #848484;

				}
			}
		}
		// .logo{
		// 	width: 135px;
		// 	height: 85px;
		// 	img{
		// 		max-width: 100%;
		// 	}
		// }
		.get-call{
			display: flex;
			align-items: center;
			.get-call-btn{
				margin-right: 20px;
				padding: 10px 20px;
				border: 1px solid #000000;

				font-weight: 500;
				font-size: 16px;
				color: #000000;
			}
		}
	}
	.nav{
